.skills-container {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.skills-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.react-logo {
  width: 19rem;
}
.skill {
  width: 6rem;
  height: 7rem;
  padding: 0;
  margin-bottom: 1rem;
}
.skill-firebase {
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 6rem;
  height: 8rem;
  padding: 0;
}
.skill-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-family: 'Patua One', sans-serif;
  font-size: 30px;
}
.skills-row {
  display: flex;
  width: 90%;
  justify-content: space-evenly;
}

.ballons {
  position: absolute;
  width: 20rem;
  top: 0;
}

@media only screen and (max-width: 768px) {
  .skills-container {
    height: 100%;
    margin-bottom: 3rem;
  }
  .skills-wrapper {
    margin-top: 2rem;
  }
  .ballons {
    display: none;
  }
  .skills-row {
    flex-direction: column;
    align-items: center;
  }
  .skill-column {
    margin-bottom: 2rem;
  }
}
