.container {
  position: relative;
  width: 50%;
  font-family: 'Roboto';
}

.image {
  opacity: 1;
  display: block;
  width: auto;
  height: 250px;
  margin: 0;
  transition: 0.5s ease;
  backface-visibility: hidden;
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  width: 250px;
  height: 250px;
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.middle p {
  padding: 2rem;
}

.container:hover .image {
  opacity: 0.3;
}

.container:hover .middle {
  opacity: 1;
}
.project-links {
  display: flex;
}
.project-links a {
  font-weight: medium;
  margin: 0rem 0.5rem 0rem 0rem;
  text-decoration: none;
  border: 1px solid black;
  border-radius: 0.5rem;
  padding: 0.5rem;
  color: #000000;
}
@media only screen and (max-width: 768px) {
  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .image {
    align-self: center;
  }
}
